import { styled } from "@rp/react/theme"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Paper, TextField, Typography } from "@rp/react/web/mui/core"
import { NameDisplay } from "../NameDisplay"

const DropdownContainer = styled(Paper, { name: "DropdownContainer" })(({ theme }) => ({
  padding: "10px 0px",
  background: theme.palette.elevation.dp24,
}))

const EditInput = styled(TextField, { name: "EditInput" })(() => ({
  "& input": {
    width: "100%",
    padding: "5px 4px",
    fontSize: 14,
  },
}))

const DropdownItemContainer = styled("div", { name: "DropdownItemContainer" })(() => {
  const { isMobile } = useDeviceInfo()
  const labelStyle = isMobile
    ? {}
    : {
        width: isMobile ? "auto" : "340px",
        "& > p": {
          overflow: "initial",
          textOverflow: "initial",
          display: "inline",
        },
      }
  return {
    "& > div": {
      display: "flex",
      alignItems: "center",
      minWidth: isMobile ? "280px" : undefined,
      width: isMobile ? undefined : "380px",
      justifyContent: "space-between",
      height: isMobile ? undefined : 34,
      padding: "0 20px",
    },
    "& div > span:nth-of-type(1)": labelStyle,
    "& div > span:nth-of-type(2)": {
      width: isMobile ? "auto" : "0px",
      visibility: isMobile ? "visible" : "hidden",
    },
    "&:hover": {
      "& div > span": {
        width: "auto",
        position: "initial",
      },
      "& div > span:nth-of-type(2)": {
        width: "auto",
        visibility: "visible",
      },
    },
  }
})

const ActionButtonsContainer = styled("span", { name: "ActionButtonsContainer" })(() => {
  const { isMobile } = useDeviceInfo()
  const labelStyle = isMobile
    ? {}
    : {
        width: isMobile ? "auto" : "340px",
        "& > p": {
          overflow: "initial",
          textOverflow: "initial",
          display: "inline",
        },
      }

  return {
    "& div > span:nth-of-type(1)": labelStyle,
    "& div > span:nth-of-type(2)": {
      width: isMobile ? "auto" : "0px",
      visibility: isMobile ? "visible" : "hidden",
    },
  }
})

const DropdownTitle = styled(Typography, { name: "DropdownTitle" })(({ theme }) => ({
  padding: "0 20px",
  marginBottom: 10,
  textTransform: "uppercase",
  letterSpacing: 0.75,
  color: theme.palette.grey[300],
  fontSize: 12,
  whiteSpace: "nowrap",
  overflow: "hidden",
  textOverflow: "ellipsis",
}))

const DropdownScroll = styled("div", { name: "DropdownScroll" })(({ theme }) => ({
  maxHeight: "170px",
  ...theme.scrollbarStyles,
  overflowX: "hidden",
  whiteSpace: "nowrap",
  textOverflow: "ellipsis",
}))

const WatchlistTitleStyled = styled("div", { name: "WatchlistTitleStyled" })(() => ({
  display: "flex",
  alignItems: "center",
  padding: "0 20px",
  justifyContent: "space-between",
}))

const MobileNameDisplay = styled(NameDisplay)(() => ({
  overflow: "visible",
  whiteSpace: "normal",
  margin: "4px 4px 4px 0",
}))

export {
  ActionButtonsContainer,
  DropdownContainer,
  DropdownItemContainer,
  DropdownTitle,
  DropdownScroll,
  WatchlistTitleStyled,
  MobileNameDisplay,
  EditInput,
}
